.textStyle{
    font-weight: bold;
    font-size: 50px;
    color: #ec3851;
    margin: 0 auto;
    word-wrap: break-word;
  }

  #border {
    border-bottom: solid 3px rgba(0,255,0,.75);
    position: absolute;
    right: 0px;
    width: 20px;
  }

  /* Animation */
  /* .textStyle {
    animation: animated-text 2s steps(30,end) 1s 1 normal both
  } */

  #border {
     -webkit-animation: animated-cursor 600ms steps(30,end) infinite;
             animation: animated-cursor 600ms steps(30,end) infinite;
  }

  /* text animation */

  @-webkit-keyframes animated-text{
    from{width: 0;}
    to{width: 100%;}
  }

  @keyframes animated-text{
    from{width: 0;}
    to{width: 100%;}
  }

  /* cursor animations */

  @-webkit-keyframes animated-cursor{
    from{border-bottom-color: rgba(0,255,0,.75);}
    to{border-bottom-color: transparent;}
  }

  @keyframes animated-cursor{
    from{border-bottom-color: rgba(0,255,0,.75);}
    to{border-bottom-color: transparent;}
  }

  /* https://stackoverflow.com/questions/18012420/draw-diagonal-lines-in-div-background-with-css */
.crossed {
  background:
      linear-gradient(to top left,
          rgba(0,0,0,0) 0%,
          rgba(0,0,0,0) calc(50% - 1.7px),
          rgba(239,71,111,1) 50%,
          rgba(0,0,0,0) calc(50% + 1.7px),
          rgba(0,0,0,0) 100%),
      linear-gradient(to top right,
          rgba(0,0,0,0) 0%,
          rgba(0,0,0,0) calc(50% - 1.7px),
          rgba(239,71,111,1) 50%,
          rgba(0,0,0,0) calc(50% + 1.7px),
          rgba(0,0,0,0) 100%);
}


.light {
    position: absolute;
    width: 0px;
    opacity: .75;
    background-color: rgb(255, 255, 255);
    box-shadow: rgb(255, 255, 255) 0px 0px 20px 2px;
  
    top: 100vh;
    bottom: 0px;
    left: 0px;
    right: 0px;
    margin: auto;
      z-index: 10;
  }
  
  .x1{
  -webkit-animation: floatUp 4s infinite linear;
  animation: floatUp 4s infinite linear;
   -webkit-transform: scale(1.0);
  transform: scale(1.0);
  }
  
  .x2{
  -webkit-animation: floatUp 7s infinite linear;
  animation: floatUp 7s infinite linear;
  -webkit-transform: scale(1.6);
  transform: scale(1.6);
  left: 15%;
  }
  
  .x3{
  -webkit-animation: floatUp 2.5s infinite linear;
  animation: floatUp 2.5s infinite linear;
  -webkit-transform: scale(.5);
  transform: scale(.5);
  left: -15%;
  }
  
  .x4{
  -webkit-animation: floatUp 4.5s infinite linear;
  animation: floatUp 4.5s infinite linear;
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  left: -34%;
  }
  
  .x5{
  -webkit-animation: floatUp 8s infinite linear;
  animation: floatUp 8s infinite linear;
  -webkit-transform: scale(2.2);
  transform: scale(2.2);
  left: -57%;
  }
  
  .x6{
  -webkit-animation: floatUp 3s infinite linear;
  animation: floatUp 3s infinite linear;
  -webkit-transform: scale(.8);
  transform: scale(.8);
  left: -81%;
  }
  
  .x7{
  -webkit-animation: floatUp 5.3s infinite linear;
  animation: floatUp 5.3s infinite linear;
  -webkit-transform: scale(3.2);
  transform: scale(3.2);
  left: 37%;
  }
  
  .x8{
  -webkit-animation: floatUp 4.7s infinite linear;
  animation: floatUp 4.7s infinite linear;
  -webkit-transform: scale(1.7);
  transform: scale(1.7);
  left: 62%;
  }
  
  .x9{
  -webkit-animation: floatUp 4.1s infinite linear;
  animation: floatUp 4.1s infinite linear;
  -webkit-transform: scale(0.9);
  transform: scale(0.9);
  left: 85%;
  }
  
  @-webkit-keyframes floatUp{
  0%{top: 100vh; opacity: 0;}
  25%{opacity: 1;}
  50%{top: 0vh; opacity: .8;}
  75%{opacity: 1;}
  100%{top: -100vh; opacity: 0;}
  }
  @keyframes floatUp{
  0%{top: 100vh; opacity: 0;}
  25%{opacity: 1;}
  50%{top: 0vh; opacity: .8;}
  75%{opacity: 1;}
  100%{top: -100vh; opacity: 0;}
  }
  
  
